.product{
    display: flex;
    flex-direction: column;
align-items: center;
/* border: 2px solid gray; */
text-align: center;
    justify-content: flex-end;
    
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    
    background-color: white;
    margin: 7px;

    z-index: 1;
    
}
.product img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 5px;
}
.product__info{
    width: 100%;
    text-align: left;
    height: 100px;
    margin-bottom: 35px;
    /* margin-top: 10px; */

}
.product__price{
    margin-top: -15px;
}

.product button{
background-color: #f0c14b;
border: 1px solid;
border-radius: 5px;
margin-top: 10px;
border-color: #a88734 #9c7e31 #846a29;
color: black;
font-size: 10px;
}
.product__rating{
    display: flex;
    /* text-align: left; */
    /* margin-bottom: 12px; */
    margin-top: -20px;
    
}



@media (max-width: 399px) {
    .product__price{
        margin-top: -10px;
    }
    .product__rating{
      
        margin-top: -10px;
        
    }
    .product__info{
        width: 100%;
        text-align: left;
        height: 100px;
        margin-bottom: 20px;
    
    
    }
}

@media (max-width: 368px) {
    .product__price{
        margin-top: -8px;
    }
    .product__rating{
      
        margin-top: -8px;
        
    }
    .product__info{
        width: 100%;
        text-align: left;
        height: 100px;
        margin-bottom: 8px;
        font-size: 9px;
    
    }


}
@media (max-width: 330px) {
    .product__price{
        margin-top: -8px;
    }
    .product__rating{
      
        margin-top: -8px;
        
    }
    .product__info{
        width: 100%;
        text-align: left;
        height: 100px;
        margin-bottom: 2px;
        font-size: 9px;
    
    }
}