.subtotal{
display: flex;
flex-direction: column;
justify-content: space-between;
width: 200px;
height: 150px;
padding: 20px;
background-color: #f3f3f3;
border: 1px solid #dddddd;
border-radius: 3px
}
.subtotal__gift{
    display: flex;
    align-items: center;
}

.subtotal input{
    cursor: pointer;
}
.button{
    background-color: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}


@media ( max-width: 438px) {
    .subtotal{
        width: 162px;
    }
   
}
@media ( max-width: 398px) {
    html{
        font-size: 10px;
    }
    
    
    .subtotal{
        width: 150px;
    }
   
}
@media ( max-width: 365px) {
    html{
        font-size: 8px;
    }
    
    
    
}
@media ( max-width: 340px) {
    html{
        font-size: 7px;
    }
    .subtotal{
        width: 140px;
    }
    
    
    
}
@media ( max-width: 320px) {
    html{
        font-size: 6px;
    }
    .subtotal{
        width: 100px;
    }
    
    
    
}