.header {
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #131921;
    position: sticky;
    top: 0;
    z-index: 100;
  }
  
  .header__logo {
    width: 70px;
    height: 70px;
    object-fit: contain;
    margin: 0 20px;
   display: flex;
   justify-content: center;
   align-items: center;
  }
  
  .header__search {
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
  }
  
  .header__searchInput {
    height: 20px;
    padding: 10px;
    border: none;
    width: 90%;

  }
  
  /* .header__search__icon {
    padding: 2px;
    height: 25px !important;
    background-color: #cd9042;
  } */
  
  .header__optionLineOne {
    font-size: 10px;
    text-decoration: none;

    
  }
  
  .header__optionLineTwo {
    font-size: 12px;
    font-weight: 800;
    text-decoration: none;

    
  }
  
  :link{
    text-decoration: none;
  }
  .header__optionBasket {
    display: flex;
    align-items: center;
    color: white;
  }
  
  .header__basketCount {
    margin-left: 10px;
    margin-right: 10px;
  }
  
  .header__nav {
    display: flex;
    justify-content: space-evenly;
    text-decoration: none;

    }
  
  .header__option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
    text-decoration: none;
  }




