
.login{
    height: 100vh;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.login__image{
    width: 100px;
margin-top: 20px;
margin-bottom: 20px;
object-fit: contain;
margin-right: auto;
margin-left: auto;
}

.login__container{
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border: 1px solid lightgray;
    border-radius: 5px;
    padding: 20px;
}

.login__container > h1 {
    font-weight: 500;
    margin-bottom: 20px;
}
.login__container > form > h1 {
    margin-bottom: 5px;
}

.login__container > form > input {
height: 30px;
margin-bottom: 10px;
background: white;
width: 98%;
}
.login__container > p {
margin-top: 15px;
font-size: 12px;

}
.login__signinbutton{
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
}

.login__registerbutton{
    border-radius: 2px;
    width: 100%;
    
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
}

